//                    import mockdata from '@/util/tabledata.js'
    export default{
        data (){
            return {
                tableData: null,
                query: {
                    topic: '',
                    meeting_time: '',
                    meeting_level: 'all',
                    meeting_state: 'all',
                    meeting_feature: 'all',
                },

                pickerOptions: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },

                meeting_level_option: [
                    {
                        label: '高级',
                        meeting_level: 'high'
                    }, {
                        label: '中级',
                        meeting_level: 'middle'
                    }, {
                        label: '普通',
                        meeting_level: 'common'
                    }, {
                        label: '不限',
                        meeting_level: 'all',
                    }
                ],

                meeting_feature_option: [
                    {
                        label: '网络会议',
                        meeting_feature: 'net',
                    }, {
                        label: '电话会议',
                        meeting_feature: 'phone'
                    }, {
                        label: '普通会议',
                        meeting_feature: 'common'
                    }, {
                        label: '不限',
                        meeting_feature: 'all'
                    }
                ],


                meeting_state_option: [
                    {
                        label: '准备',
                        meeting_state: 'prepare'
                    }, {
                        label: '进行中',
                        meeting_state: 'ing',
                    }, {
                        label: '已完成',
                        meeting_state: 'complete',
                    }, {
                        label: '不限',
                        meeting_state: 'all',
                    }
                ],

//                tableData: [],
                pageSize: 10,
                currentPage: 1,
                total: 1,


            };
        },

        mounted: function () {
            this.getTableData();
        },

        methods: {
            getTableData() {
                let _this = this;
                axios.post('/getTableData', {
                    data: _this.query,
                    currentPage: _this.currentPage,
                    pageSize: _this.pageSize
                }).then(function (response) {

                    console.log(response.data);
                    var data = response.data;
                    console.log("data is  " + data);
                    _this.tableData = data.meetings;
                    _this.total = data.total;

                }).catch(function (error) {
                    console.log(error)
                })
            },

            handleSizeChange(val){
                this.pageSize = val;
                this.currentPage = 1;
                this.getTableData();
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getTableData();
            }
        },

    }





